import React, {useState} from "react";

function Checkout({products}) {
   const  countBuy = products.filter((countBuy) => {
       return countBuy.bought;
   })
    return(
        <div className="checkout">
            <a href="#">
                <img src="http://intocode.ru/d/react-project-1/build/static/media/shopping-cart-solid.8bf1af37.svg" alt=""/>
                <span className="counter">
                    {countBuy.length}
                </span>
            </a>
        </div>
    )
}
export default Checkout;